<template>
	<div class="details">
		<div class="title font-center">{{sourceData.title}}</div>

		<div class="day-price" v-if="sourceData.day || sourceData.price">
			<div class="day" v-if="sourceData.day">{{sourceData.day}}</div>
			<div class="price" v-if="sourceData.price">¥{{sourceData.price}}万起/人</div>
		</div>

		<div
			:class="['dsc-' + detail.type]"
			v-for="detail in sourceData.details"
		>
			<img :src="detail.content" v-if="detail.type == 'img'">
			<div v-if="detail.type == 'txt'">
				<span :class="[content.size && 'font-' + content.size, { 'font-medium': content.font == 'medium' }]" v-for="content in detail.content">
					<a :href="content.url" v-if="content.target != 'router-link'">{{content.content}}</a>
					<router-link tag="a" :to="{ path: content.url }" v-if="content.target == 'router-link'">{{content.content}}</router-link>
				</span>
			</div>
			<div class="table" v-if="detail.type == 'table'">
				<div class="th">
					<div :class="[th.align && 'font-' + th.align, th.font == 'medium' && 'font-medium']" v-for="th in detail.content.th">{{th.content}}</div>
				</div>
				<div class="tr">
					<div :class="[tr.align && 'font-' + tr.align, tr.font == 'medium' && 'font-medium']" v-for="tr in detail.content.tr">{{tr.content}}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				sourceData: {}
			}
		},
		created() {
			this.sourceData = JSON.parse(sessionStorage.getItem('detail'));
		}
	}
</script>

<style lang="scss">
	@import './../../styles/detail/detail.scss';
